<template>
<div class="hs-container">
  <c-header leftIcon1="home_2.png" :leftFn1="_gotoHomePage" :title="$t('base.IntlFlightsOrderConfirmation')"></c-header>
  <hs-scroll class="table-view">
    <ul>

      <!-- 订单信息 -->
      <hs-section>
        <hs-cell-1 :showLogo="false">
          <div class="state-details">
            <div class="header">
              <p>{{Number(intlFlightOrder.TravelType) === 0 ? $t('base.BusinessTrip') : $t('base.PersonalTrip')}}</p>
            </div>
            <!-- <p class="infor">{{$t('base.OrderNumber')}}: {{intlFlightOrder.TravelID}}</p> -->
            <p class="infor">{{$t('base.OrderDate')}}: {{$t(beTime(new Date()), 'date', ['yyyy-MM-dd', 'MM dd, yyyy'])}}&nbsp;{{beTime(new Date(), 'hh:mm')}}</p>
            <!-- <div class="buttons" v-if="!submitSuccess">
              <hs-button class="button" v-if="Number(intlFlightOrder.PayType) === 2 && noValue(JSON.stringify(intlFlightOrder.VettingProcessList))" @click="_gotoSubmitAndPay">{{$t('base.SubmitAndPay')}}</hs-button>
              <hs-button class="button" v-else @click="_confirm">{{$t('base.Submit')}}</hs-button>
            </div>
            <div class="buttons" v-else-if="submitSuccess && Number(intlFlightOrder.PayType) === 2 && noValue(JSON.stringify(intlFlightOrder.VettingProcessList))">
              <hs-button class="button" @click="_gotoPay">{{$t('base.Pay')}}</hs-button>
            </div> -->
            <div class="buttons">
              <hs-button v-if="intlFlightOrder.PageBtnInfo.IsAllowPayAndSubmit" @click="_gotoSubmitAndPay"
                class="button">{{$t('base.SubmitAndPay')}}</hs-button>
              <hs-button v-if="intlFlightOrder.PageBtnInfo.IsAllowSubmit" @click="_confirm"
                class="button">{{$t('base.Submit')}}</hs-button>
              <hs-button v-if="intlFlightOrder.PageBtnInfo.IsAllowPay" @click="_gotoPay"
                class="button">{{$t('base.Pay')}}</hs-button>
            </div>
          </div>
        </hs-cell-1>
      </hs-section>

      <!-- 行程信息 -->
      <hs-section backgroundColor="#F6F7F8">
        <div class="custom-cell-segments">
          <div class="segments" @click="_openSegmentDetailsDialog">
            <div class="details">
              <div class="item" v-for="(item, index) in intlFlightOrder.SelectedSegment" :key="index">
                <div class="mark">{{_showSegmentTitle(index)}}</div>
                <p>{{item.DepartDate}}</p>
                <p>{{item.DepartDayOfWeek}}</p>
                <p>{{item.DepartDateTime.split(' ')[1]}}</p>
                <p>{{intlFlightOrder.SearchParams.Segments[index].DepartObject[`AirPortName_${language}`]}}-{{intlFlightOrder.SearchParams.Segments[index].ArrivalObject[`AirPortName_${language}`]}}</p>
              </div>
            </div>
            <div class="logo" style="background-image: url('static/image/base/cell/right_arrow_gray.png')"></div>
          </div>
          <div class="infor" v-if="details && details.ChargeInfoList">
            <div>
              <p>{{$t('base.AirTickets')}}<span>¥{{details.ChargeInfoList[0].Price}}</span></p>
              <p>{{$t('base.Tax')}}<span>¥{{details.ChargeInfoList[0].Tax}}</span></p>
              <p v-if="details.ChargeInfoList[0].Rebate > 0">{{$t('base.FrontReturn')}}<span>-¥{{details.ChargeInfoList[0].Rebate}}</span></p>
            </div>
            <p>退改签 ></p>
          </div>
        </div>
      </hs-section>

      <!-- 乘客信息 -->
      <hs-section>
        <hs-cell-1 :showLogo="false" :showSeparator="false" v-for="(item, index) in intlFlightOrder.Passengers" :key="index">
          <div class="custom-cell-people">
            <div class="left" :style="{opacity: index === 0 ? 1 : 0}">
              <p>{{$t('base.Passenger')}}</p>
            </div>
            <div class="right" :style="{'border-bottom-width': index === intlFlightOrder.Passengers.length - 1 ? '0px' : '1px'}">
              <div class="name">{{item.Name}}</div>
              <div class="others">
                <p>{{item.Credential.CredentialName}} {{item.Credential.CredentialNo}}</p>
                <!-- <p class="insure" v-for="(itemChild, indexChild) in item.Insurances" :key="indexChild">
                  {{itemChild.InsuranceCategory}}
                  <template v-if="item.Order && !noValue(intlFlightOrder.BackSelectedFlight.FlightNo)">({{itemChild.Order === 1 ? $t('base.Going') : $t('base.Return')}})</template>
                  x 1{{$t('base.Copy')}}
                </p> -->
              </div>
            </div>
          </div>
        </hs-cell-1>
      </hs-section>

      <!-- 其他信息 -->
      <hs-section v-if="details.IsDisplayCustomItem || details.IsDisplayPurpose || details.IsDisplayAuthorizationCode || Number(intlFlightOrder.TravelType) === 0 || intlFlightOrder.ViolationCabinRankReason">
        <hs-cell-small-title v-if="details.IsDisplayCustomItem && intlFlightOrder.CustomItem && intlFlightOrder.CustomItem.Name"
          :title="details.CustomItemName"
          type="bold"
          :value="intlFlightOrder.CustomItem.Name"
          :showLogo="false">
        </hs-cell-small-title>
        <hs-cell-small-title v-if="details.IsDisplayPurpose"
          :title="$t('base.TravelPurpose')"
          type="bold"
          :value="intlFlightOrder.Purpose"
          :showLogo="false">
        </hs-cell-small-title>
        <hs-cell-small-title v-if="details.IsDisplayAuthorizationCode"
          :title="$t('base.AuthorizationCode')"
          type="bold"
          :value="intlFlightOrder.AuthorizationCode"
          :showLogo="false">
        </hs-cell-small-title>
        <hs-cell-small-title v-if="Number(intlFlightOrder.TravelType) === 0" @click.native="showCExamineProcess = true"
          :class="{disabled: noValue(JSON.stringify(intlFlightOrder.VettingProcessList))}"
          :title="$t('base.ApprovalInformation')"
          type="bold"
          :value="noValue(JSON.stringify(intlFlightOrder.VettingProcessList)) ? $t('base.NoApprovalRequired') : (language === 'CN' ? '' : $t('base.ApprovalInformation'))"
          :showLogo="!noValue(JSON.stringify(intlFlightOrder.VettingProcessList))">
        </hs-cell-small-title>
        <hs-cell-small-title v-if="Number(intlFlightOrder.TravelType) === 0"
          :title="$t('base.PaymentWay')"
          type="bold"
          :value="intlFlightOrder.PayType | filterPayType"
          :showLogo="false">
        </hs-cell-small-title>
      </hs-section>

      <!-- 联系人 -->
      <hs-section>
        <hs-cell-1 :showLogo="false" :showSeparator="false" v-for="(item, index) in intlFlightOrder.Contacts" :key="index">
          <div class="custom-cell-people">
            <div class="left" :style="{opacity: index === 0 ? 1 : 0}">
              <p>{{$t('base.ContactInformation')}}</p>
            </div>
            <div class="right" :style="{'border-bottom-width': index === intlFlightOrder.Contacts.length - 1 ? '0px' : '1px'}">
              <div class="name">{{item.Name}}</div>
              <div class="others">
                <p>{{$t('base.CellphoneNumber')}} {{item.Mobile ? item.Mobile : $t('base.Unfilled')}}</p>
                <p>{{$t('base.Email')}} {{item.Email ? item.Email : $t('base.Unfilled')}}</p>
              </div>
            </div>
          </div>
        </hs-cell-1>
      </hs-section>

    </ul>
  </hs-scroll>

  <div class="footer" @click="_switchDetailsDialog">
    <div class="price">
      <p>{{$t('base.TotalAmount')}}</p>
      <p>￥{{intlFlightOrder.TotalPrice}}</p>
    </div>
    <div class="details">
      <div class="logo" :style="{transform: showDetailsDialog ? 'rotate(90deg)' : 'rotate(-90deg)'}" style="background-image: url('static/image/base/cell/right_arrow_gray.png')"></div>
    </div>
  </div>

  <!-- 价格明细 -->
  <hs-dialog v-model="showDetailsDialog" height="calc(100% - 50px)" ref="detailsDialog">
    <hs-scroll class="detailsDialog" :isListenScroll="true" @scrollStart="_scrollStart" @click.native.stop>
      <ul>
        <hs-section v-if="details.ChargeInfoList && details.ChargeInfoList[0]">
          <hs-cell-1 :showLogo="false" v-if="details.ChargeInfoList[0].Price >= 0">
            <div class="detailsDialog-custom-cell">
              <p>{{$t('base.AirTickets')}}</p>
              <p><span>￥{{details.ChargeInfoList[0].Price}}</span><span>x {{intlFlightOrder.Passengers.length}}{{$t('base.QuantifierPeople')}}</span></p>
            </div>
          </hs-cell-1>
          <hs-cell-1 :showLogo="false" v-if="details.ChargeInfoList[0].Tax >= 0">
            <div class="detailsDialog-custom-cell">
              <p>{{$t('base.Tax')}}</p>
              <p><span>￥{{details.ChargeInfoList[0].Tax}}</span><span>x {{intlFlightOrder.Passengers.length}}{{$t('base.QuantifierPeople')}}</span></p>
            </div>
          </hs-cell-1>
          <hs-cell-1 :showLogo="false" v-if="details.ChargeInfoList[0].Rebate > 0">
            <div class="detailsDialog-custom-cell">
              <p>{{$t('base.FrontReturn')}}</p>
              <p><span>-￥{{details.ChargeInfoList[0].Rebate}}</span><span>x {{intlFlightOrder.Passengers.length}}{{$t('base.QuantifierPeople')}}</span></p>
            </div>
          </hs-cell-1>
          <hs-cell-1 :showLogo="false" v-if="details.ChargeInfoList[0].ServicePrice >= 0">
            <div class="detailsDialog-custom-cell">
              <p>{{$t('base.ServiceFee')}}</p>
              <p><span>￥{{details.ChargeInfoList[0].ServicePrice}}</span><span>x {{intlFlightOrder.Passengers.length}}{{$t('base.QuantifierPeople')}}</span></p>
            </div>
          </hs-cell-1>
          <hs-cell-1 v-for="(item, index) in details.InsuranceProducts.filter(v => v.select)" :key="index"
            :showLogo="false">
            <div class="detailsDialog-custom-cell">
              <p>{{item.ClientShowName}}&nbsp;|&nbsp;{{_showSegmentTitle(0)}}</p>
              <p><span>￥{{item.SalePrice}}</span><span>x {{intlFlightOrder.Passengers.length}}{{$t('base.Copy')}}</span></p>
            </div>
          </hs-cell-1>
        </hs-section>
      </ul>
    </hs-scroll>
    <div class="show-more" ref="show-more">{{$t('base.SlideUpToSeeMore')}}</div>
  </hs-dialog>

  <!-- 航班详情 -->
  <hs-dialog v-model="showSegmentDetailsDialog" ref="SegmentDetails">
    <div class="segmentDetails" @click.native.stop>
      <div class="header">
        <div class="logo"></div>
        <p>航班详情</p>
        <div @click="$refs.SegmentDetails.showDetails = false"
          class="logo" style="background-image: url('static/image/plane-ticket/query/close.png')"></div>
      </div>
      <hs-scroll
        class="details"
        ref="segmentTableView"
        :data="[intlFlightOrder.SelectedSegment]">
        <ul>
          <div class="item" v-for="(item, index) in intlFlightOrder.SelectedSegment" :key="index">
            <div class="item-header">
              <div class="mark">{{_showSegmentTitle(index)}}</div>
              <p>{{item.DepartDate}}</p>
              <p>{{item.DepartDayOfWeek}}</p>
              <p>{{item.DepartDateTime.split(' ')[1]}}</p>
              <p>{{intlFlightOrder.SearchParams.Segments[index].DepartObject[`AirPortName_${language}`]}}-{{intlFlightOrder.SearchParams.Segments[index].ArrivalObject[`AirPortName_${language}`]}}</p>
            </div>
            <segment :datalist="item.FlightLegs"></segment>
          </div>
        </ul>
      </hs-scroll>
    </div>
  </hs-dialog>

  <!-- 审批信息 -->
  <c-examine-process v-model="showCExamineProcess" :details="intlFlightOrder" :passengerWarning="$t('base.Passenger')"></c-examine-process>

  <!-- 支付 -->
  <pay ref="Pay"></pay>

  <c-order-success ref="COrderSuccess" :XOrder="intlFlightOrder" :OrderBusinessType="6"></c-order-success>

</div>
</template>
<script type="text/ecmascript-6">
import Vue from 'vue'
import CExamineProcess from 'components/c-examine-process/c-examine-process-confirm'
import CHeader from 'components/c-header/c-header'
import COrderSuccess from 'components/c-order-success/c-order-success.vue'
import HsButton from 'base/hs-button/hs-button'
import HsCell1 from 'base/hs-tableview/hs-cell-1/hs-cell-1'
import HsCellSmallTitle from 'base/hs-tableview/hs-cell-small-title/hs-cell-small-title'
import HsDialog from 'base/hs-dialog/hs-dialog'
import HsScroll from 'base/hs-scroll/hs-scroll'
import HsSection from 'base/hs-tableview/hs-section/hs-section'
import Pay from 'pages/pay/pay'
import Segment from 'pages/intl-plane-ticket/book/infor-list/segment'

import {mapGetters, mapMutations} from 'vuex'

import {kpiIntlflightConfirmIntlOrder} from 'api'
export default {
  created() {
    if (this.noValue(this.intlFlightOrder.TravelType)) {
      this._gotoHomePage()
    }
  },
  computed: {
    ...mapGetters([
      'intlFlightOrder'
    ]),
    details() {
      return this.$route.params.details
    }
  },
  data() {
    return {
      submitSuccess: false,
      /** 行程 */
      showSegmentDetailsDialog: false,
      /** 价格明细 */
      showDetailsDialog: false,
      /** 审批信息 */
      showCExamineProcess: false
    }
  },
  filters: {
    filterPayType(val) {
      switch (val) {
        case 1:
          return Vue.prototype.$t('base.CompanyPayment')
        case 2:
          return Vue.prototype.$t('base.PersonalPayment')
        case 3:
          return `${Vue.prototype.$t('base.CompanyPayment')}(${Vue.prototype.$t('base.Prepayments')})`
        default:
          return Vue.prototype.$t('base.Unknown')
      }
    }
  },
  methods: {
    ...mapMutations([
      'SET_INTL_FLIGHT_ORDER'
    ]),
    _gotoHomePage() {
      this.$router.replace('/home')
    },

    /** 根据航段显示单程/往返程/N程 */
    _showSegmentTitle(index) {
      let length = this.intlFlightOrder.SelectedSegment && !this.noValue(JSON.stringify(this.intlFlightOrder.SelectedSegment)) ? this.intlFlightOrder.SelectedSegment.length : 0
      if (length === 1) {
        return this.$t('base.OneWay')
      } else if (length === 2) {
        if (this.intlFlightOrder.SelectedSegment[0].DepartAirportName === this.intlFlightOrder.SelectedSegment[1].ArrivalAirportName) {
          return index === 0 ? this.$t('base.Going') : this.$t('base.Return')
        } else {
          return this.$t('base.NoXSegment', 'function', index + 1)
        }
      } else {
        return this.$t('base.NoXSegment', 'function', index + 1)
      }
    },

    /** 提交并支付 */
    _gotoSubmitAndPay() {
      this.$alert({
        content: this.$t('TM.AreYouSureToSubmitAndPayForTheOrder'),
        cancelTxt: this.$t('base.Cancel'),
        confirmFn: () => {
          let params = {
            OrderIDs: this.intlFlightOrder.OrderIDs
          }
          kpiIntlflightConfirmIntlOrder(params).then((res) => {
            console.log('提交并支付: ', res)
            if (res.ResultData === true) {
              this.submitSuccess = true
              this._gotoPay()
            } else {
              this.$toast(this.$t('TM.TheSubmissionFailedPleaseTryAgainLater'))
            }
          })
        }
      })
    },

    /** 提交 */
    _confirm() {
      this.$alert({
        content: this.$t('TM.AreYouSureToSubmitYourOrder'),
        cancelTxt: this.$t('base.Cancel'),
        confirmFn: () => {
          let params = {
            OrderIDs: this.intlFlightOrder.OrderIDs
          }
          kpiIntlflightConfirmIntlOrder(params).then((res) => {
            console.log('确认出票: ', res)
            if (res.ResultData === true) {
              this.submitSuccess = true
              /** 提交成功后 */
              this.$refs.COrderSuccess.open()
              // this.$alert({
              //   content: Number(this.intlFlightOrder.PayType) === 2 ? this.$t('TM.TheCurrentOrderIsAPersonalPaymentOrder') : this.$t('TM.OrderSubmissionSuccessful'),
              //   confirmTxt: this.$t('base.CheckOrder'),
              //   cancelTxt: this.$t('base.ContinueBooking'),
              //   confirmFn: () => {
              //     this.$router.replace({name: 'intlPlaneTicketOrderList', params: {TravelType: Number(this.intlFlightOrder.TravelType)}})
              //   },
              //   cancelFn: () => {
              //     this.$router.replace({path: '/planeTicket/query', query: {TravelType: this.intlFlightOrder.TravelType}})
              //   }
              // })
              /********************************************************************************************************************/
            } else {
              this.$toast(this.$t('TM.TheSubmissionFailedPleaseTryAgainLater'))
            }
          })
        }
      })
    },

    /** 支付 */
    _gotoPay() {
      this.$refs.Pay && this.$refs.Pay.gotoPay({
        OrderBusinessType: 6,
        OrderID: this.intlFlightOrder.OrderIDs[0],
        PayWay: 3,
        TravelType: Number(this.intlFlightOrder.TravelType),
        CallbackPathName: 'intlPlaneTicketOrderList'
      })
    },

    /** 打开行程详情 */
    _openSegmentDetailsDialog() {
      this.showSegmentDetailsDialog = true
      setTimeout(() => {
        this.$refs.segmentTableView.forceUpdate()
      }, 500)
    },

    /** 价格明细 */
    _switchDetailsDialog() {
      if (this.showDetailsDialog) {
        this.$refs.detailsDialog.showDetails = false
      } else {
        this.showDetailsDialog = true
        setTimeout(() => {
          let dom = this.$refs.detailsDialog.$children[0].scroll
          if (dom.wrapperHeight < dom.scrollerHeight) {
            this.$refs['show-more'].style.opacity = 1
          } else {
            this.$refs['show-more'].style.opacity = 0
          }
        }, 400)
      }
    },

    /** 上拉查看更多 */
    _scrollStart() {
      this.$refs['show-more'].style.opacity = 0
    }
  },
  components: {
    CExamineProcess,
    CHeader,
    COrderSuccess,
    HsButton,
    HsCell1,
    HsCellSmallTitle,
    HsDialog,
    HsScroll,
    HsSection,
    Pay,
    Segment
  }
}
</script>
<style scoped lang="stylus" rel="stylesheet/stylus">
@import '~common/stylus/base'
@import '~common/stylus/variable'
.hs-container
  width 100%
  height 100%
  >.table-view
    width 100%
    height calc(100% - 45px - 50px)
    overflow hidden

    .state-details
      width 100%
      padding 15px 0
      background-color $color_white
      >.header
        @extend .flex-row
        @extend .flex-between
        width 100%
        margin-bottom 8px
        font-size 1.05rem
        // >p
        //   &:last-child
        //     visibility hidden
      >.infor
        width 100%
        font-size 0.8rem
        color $color_gray_text
        line-height 1.4em
      >.buttons
        @extend .flex-row
        width 100%
        >.button
          flex 1
          margin 10px 5px 0 5px
          &:first-child
            margin-left 0px
          &:last-child
            margin-right 0px

    // 行程
    .custom-cell-segments
      width calc(100% - 30px)
      margin-left 15px
      padding 0 15px
      background-color $color_white
      border-radius 5px
      box-shadow 0 0 10px $color_gray_boxshadow
      >.segments
        @extend .flex-row
        @extend .flex-between
        width 100%
        padding 5px 0
        border-bottom 1px solid $color_gray_layer
        >.details
          width calc(100% - 25px)
          >.item
            @extend .flex-row
            @extend .flex-start
            flex-wrap wrap
            width 100%
            padding 10px 0
            border-bottom 1px solid $color_gray_layer
            &:last-child
              border-bottom-width 0px
            >.mark
              font-size 1rem
              color $color_theme
              font-weight bold
              padding 5px
              border-radius 2px
            >p
              font-size 1rem
              margin-left 5px
              line-height 1.2em
        >.logo
          width 20px
          height 20px
          background-size 100% 100%
      >.infor
        @extend .flex-row
        @extend .flex-between
        width 100%
        padding 15px 0
        font-size 0.8rem
        >div
          @extend .flex-column
          @extend .flex-top
          width calc(100% - 80px)
          >p
            margin-bottom 6px
            &:last-child
              margin-bottom 0
            >span
              color $color_theme
              margin-left 6px
        >p
          width 80px
          color $color_gray_text
          text-align right

    // 乘客信息
    .custom-cell-people
      @extend .flex-between
      @extend .flex-start
      @extend .flex-top
      width 100%
      >.left
        width 90px
        padding 15px 0
        >p
          @extend .flex-row
          @extend .flex-start
          font-size 1rem
          color $color_black_text_1
          line-height 1.4em
      >.right
        width calc(100% - 100px)
        padding 15px 0
        border-bottom 1px dashed $color_gray_layer
        >.name
          @extend .flex-row
          @extend .flex-start
          font-size 1rem
          font-weight bold
          line-height 1.4em
        >.others
          width 100%
          >p
            line-height 1.5em
            color $color_gray_text
            font-size 1rem
            &:first-child
              margin-top 7px
          >.insure
            font-size 0.8rem

  // footer 价格明细
  >.footer
    @extend .flex-row
    @extend .flex-between
    width 100%
    height 50px
    background-color $color_white
    border-top 1px solid $color_gray_layer
    padding 0 15px
    >.price
      @extend .flex-row
      @extend .flex-start
      width calc(100% - 30px)
      height 100%
      >p
        font-size 1rem
        &:nth-child(2)
          color $color_theme
          margin-left 5px
    >.details
      @extend .flex-row
      @extend .flex-between
      width 20px
      height 100%
      >p
        font-size 0.9rem
      >.logo
        width 20px
        height 20px
        background-size 100% 100%
        transition all 300ms
  .show-more
    @extend .flex-center
    position absolute !important
    width 100%
    left 0
    bottom 0
    height 15px
    color $color_black_text_0
    font-size 0.6rem
    transition all 300ms
  .detailsDialog
    position absolute !important
    width 100%
    left 0
    bottom 0
    background-color $color_white
    max-height 70%
    overflow hidden
    .detailsDialog-custom-cell
      @extend .flex-row
      @extend .flex-between
      width 100%
      height 44px
      >p
        font-size 0.8rem
        &:nth-child(1)
          color $color_black_text_0
        &:nth-child(2)
          color $color_theme
          >span
            &:nth-child(2)
              color $color_black_text_0
              margin-left 5px

  // 航班详情
  .segmentDetails
    position absolute !important
    width 100%
    left 0
    bottom 0
    background-color $color_white
    max-height 70%
    overflow hidden
    border-radius 10px 10px 0 0

    >.header
      @extend .flex-row
      @extend .flex-between
      width 100%
      height 45px
      border-bottom 1px solid $color_gray_layer
      padding 0 15px
      >.logo
        width 20px
        height 20px
        background-size 100% 100%
      >p
        font-size 1.1rem
        font-weight bold

    >.details
      width 100%
      height 400px
      overflow hidden
      .item
        width 100%
        padding 15px
        border-bottom 1px solid $color_gray_layer
        &:last-child
          border-bottom-width 0
        >.item-header
          @extend .flex-row
          @extend .flex-start
          flex-wrap wrap
          width 100%
          >.mark
            font-size 1rem
            color $color_theme
            font-weight bold
            padding 5px
            border-radius 2px
          >p
            font-size 1rem
            margin-left 5px
            line-height 1.2em
</style>



